.xcy-shade{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    /*transition: opacity .5s;*/
    @include animation( opacityin .5s );
}
.xcy-layer, .xcy-loading{
    position: fixed;
    @include backface-visibility( hidden );
    @include perspective( 1000 );
}
.xcy-loading{
    width: 74px;
    height: 74px;
    z-index: 999901;
    @include transform( translate3d(-50%, -50%, 0) );
}
.xcy-main{
    display: table;
}
.xcy-shade{
    background-color: rgba(0,0,0, 0.6);
    pointer-events: auto;
}
.xcy-section{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}
.xcy-box, .xcy-load-box{
    width: auto;
    max-width: 260px;
    min-width: 40px;
    position: relative;
    display: inline-block;
    color: #333;
    background-color: rgba(255,255,255, 1);
    border-radius: 6px;
}
.xcy-box{
    width: 280px;
}
.xcy-load-box{
    padding: 12px;
    color: #fff;
    background-color: rgba(0,0,0, 0.8);
}
.xcy-con{
    padding: 20px;
}
.xcy-btns{
    border-top: 1px solid #ccc;
    color: #6599ff;
    @include displayflex();
}
.xcy-btns div{
    text-align: center;
    cursor: pointer;
    padding: 12px 0;
    @include flex( 1 );
}
.xcy-btn{
    @include box-sizing( border-box );
}
.xcy-btns div{
    &:nth-child(2){
        border-left: 1px solid #ccc;
    }
    &:active{
        opacity: 0.75;
    }
}
.xcy-load{
    vertical-align: top;
    width: 50px;
    height: 50px;
    margin: 0 auto;
    background:url(data:image/png;base64,) 0 0 no-repeat;
    -webkit-background-size:50px 50px;
    background-size:50px 50px;
    background-position:0 100%;
    @include animation(loading 2s linear 0ms infinite normal);
}

.popscalein{
    @include animation(popscalein 600ms cubic-bezier(0.680,0,0.265,1));
}
.popscaleout{
    @include animation(popscaleout 600ms cubic-bezier(0.680,0,0.265,1));
}

.slideleftin{
    @include animation(slideleftin 350ms ease-in-out);
}
.slideleftout{
    @include animation(slideleftout 350ms ease-in-out);
}

.sliderightin{
    @include animation(sliderightin 350ms ease-in-out);
}
.sliderightout{
    @include animation(sliderightout 350ms ease-in-out);
}

.slidedownin{
    @include animation(slidedownin 350ms ease-in-out);
}
.slidedownout{
    @include animation(slidedownout 350ms ease-in-out);
}

.slideupin{
    @include animation(slideupin 350ms ease-in-out);
}
.slideupout{
    @include animation(slideupout 350ms ease-in-out);
}

.opacityin{
    @include animation(opacityin 350ms ease-in-out);
}
.opacityout{
    @include animation(opacityout 350ms ease-in-out);
}

@include keyframes( loading ){
    from {
        -webkit-transform:rotate(0deg) translateZ(0);
    }
    to {
        -webkit-transform:rotate(360deg) translateZ(0);
    }
}
// 渐显示
@include keyframes( opacityin ){
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
// 渐隐藏
@include keyframes( opacityout ){
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
// 弹出显示
@include keyframes( popscalein ){
    0%{
        @include transform( scale(0,0) );
        opacity: 0;
    }
    45%{
        @include transform( scale(1.2,1.2) );
        opacity: 1;
    }
    100%{
        @include transform( scale(1,1) );
        /*opacity: 1;*/
    }
}
// 弹出隐藏
@include keyframes( popscaleout ){
    0%{
        @include transform( scale(1,1) );
        opacity: 1;
    }
    45%{
        @include transform( scale(1.2,1.2) );
        opacity: 1;
    }
    100%{
        @include transform( scale(0,0) );
        opacity: 0;
    }
}
// 向右渐显
@include keyframes( slideleftin ){
    from {
        @include transform( translateX(-100%) );
    }
    to {
        @include transform( translateX(0px) );
    }
}
// 向右渐隐
@include keyframes( slideleftout ){
    from {
        @include transform( translateX(0px) );
    }
    to {
        @include transform( translateX(-100%) );
    }
}
// 向左渐显
@include keyframes( sliderightin ){
    from {
        @include transform( translateX(100%) );
    }
    to {
        @include transform( translateX(0px) );
    }
}
// 向左渐隐
@include keyframes( sliderightout ){
    from {
        @include transform( translateX(0px) );
    }

    to {
        @include transform( translateX(100%) );
    }
}
// 向上渐显
@include keyframes( slideupin ){
    from {
        @include transform( translate3d(0,100%,0) );
    }

    to {
        @include transform( translate3d(0,0,0) );
    }
}
// 向上渐隐
@include keyframes( slideupout ){
    from {
        @include transform( translate3d(0,0,0) );
    }

    to {
        @include transform( translate3d(0,100%,0) );
    }
}
// 向下渐显
@include keyframes( slidedownin ){
    from {
        @include transform( translate3d(0,-100%,0) );
    }

    to {
        @include transform( translate3d(0,0,0) );
    }
}
// 向下渐隐
@include keyframes( slidedownout ){
    from {
        @include transform( translate3d(0,0,0) );
    }

    to {
        @include transform( translate3d(0,-100%,0) );
    }
}